.table-component {
    width: calc(100% - 185px);
}

.table-wrapper {
    overflow: auto;
    margin: 20px;
}

table {
    color: rgba(0, 0, 0, 0.87);
    font-size: 10px;
    font-family: "Roboto";
    /* the shape of the out line */
    border-collapse: collapse;
    /* the thickness and the color of the outer line of the table */
    border: 0.5px solid lightgrey;
    /*the space bewteen the letters inside the table  */
    letter-spacing: 0.8px;
    /* the size of the word inside the table */
    box-shadow: 0px 3px 5px 0px rgba(0, 0, 0, 0.2), 0px 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 3px 1px -2px rgba(0, 0, 0, 0.12);
    background-color: #e0e0e0;
}

td, th {
    /* color : black; */
    /* the thickness of the table outer line */
    /* border: 0.1px solid rgb(190,190,190); */
    /* the width and the hight of the table */
    padding: 8px 25px;
}

/* jjjj */

th {
    /* the color of the head row */
    /* box-shadow: 0px 3px 5px 0px rgba(0,0,0,0.2), 0px 2px 2px 0px rgba(0,0,0,0.14), 0px 3px 1px -2px rgba(0,0,0,0.12); */
    background-color: #c9d1cf;
}

td {
    /* the location of the date inside the table, the data part, not the head row */
    text-align: center;
}

tr:nth-child(even) td {
    /* the color of even row */
    background-color: rgb(250, 250, 250);
}

tr:nth-child(odd) td {
    /* the color of odd rows */
    background-color: rgb(245, 245, 245);
}

caption {
    padding: 10px;
}